import ServiceDrawer from '../service-drawer/service-drawer.js';

import styles from './service-sections.module.css';

export default function ServiceSections({ servicesData, updateParent }) {
  const serviceSections = servicesData?.map(serviceSection =>
    <div key={serviceSection.title}>
      <div className={styles.sectionTitle}>
        {serviceSection.title}
      </div>
      <div className={styles.serviceDrawer}>
        <ServiceDrawer serviceSection={serviceSection.subsections} updateParent={updateParent} />
      </div>
    </div>
  );

  return (
    <div>
      {serviceSections}
    </div>
  );
}