import Accordion from '@mui/material/Accordion';
import AccordionActions from '@mui/material/AccordionActions';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

import ServiceDetails from '../service-details/service-details.js';

export default function ServiceDrawer({ serviceSection, updateParent }) {
  const drawers = serviceSection?.map(section =>
    <Accordion key={section.title}>
      <AccordionSummary
        id="panel-header"
        aria-controls="panel-content"
        expandIcon={<ExpandMoreIcon />}
      >
        {section.title}
      </AccordionSummary>
      <AccordionDetails>
        <ServiceDetails services={section.services} updateParent={updateParent} />
      </AccordionDetails>
    </Accordion>
  );

  return (
    <div>
      {drawers}
    </div>
  );
}