export const mergeData = (pricingSheets, additionalData) => {
  // Need to iterate through each item, but these are nested a few levels deep
  for (let i = 0; i < pricingSheets.length; i++) {
    const subsections = pricingSheets[i].subsections;
    for (let j = 0; j < subsections.length; j++) {
      const servicesForSubsection = subsections[j].services;
      for (let k = 0; k < servicesForSubsection.length; k++) {
        var service = servicesForSubsection[k];

        const additionalDataForService = additionalData.dibsAndQuestions.find(obj => {
          if (obj.Item === service.Item) {
          }
          return obj.Item === service.Item;
        });

        service.questionsBlock = additionalDataForService?.Questions;
        service.dibs = additionalDataForService?.Dibs;
      }
    }
  }
  return pricingSheets;
};