import { useState, useEffect } from 'react';
import './App.css';
import logo from './osBoosting.png';
import { createTheme, ThemeProvider } from "@mui/material/styles";

import ServiceSections from './components/service-sections/service-sections.js';
import { fetchPricingData } from './network/fetch-pricing.js';
import { fetchAdditionalData } from './network/fetch-my-additions.js';
import { mergeData } from './helperMethods/merge-data.js';
import PostingSpace from './components/posting-space/posting-space.js';

const darkTheme = createTheme({
  palette: {
    mode: 'dark',
  },
});

function App() {
  const [pricingSheets, setPricingSheets] = useState([]);
  const [selectedServices, setSelectedServices] = useState([]);

//  useEffect(() => {
//    fetchPricingData()
//      .then(data => {
//        setPricingSheets(data);
//      })
//      .catch(error => {
//        console.error(error);
//      })
//  }, []);
//
//  useEffect(() => {
//    fetchAdditionalData()
//      .then(additionalData => {
//        const mergedPricingSheetData = mergeData(pricingSheets, additionalData);
//        console.log('mergedData', mergedPricingSheetData);
//        setPricingSheets(mergedPricingSheetData);
//      })
//      .catch(error => {
//        console.error(error);
//      })
//  }, []);

  useEffect(() => {
    fetchPricingData()
      .then(pricingData => {
        fetchAdditionalData()
          .then(additionalData => {
            const mergedPricingSheetData = mergeData(pricingData, additionalData);
            setPricingSheets(mergedPricingSheetData);
          })
      })
      .catch(error => {
        console.error(error);
      })
  }, []);

  function checkboxUpdated(service, checked) {
    if (checked) {
      setSelectedServices([...selectedServices, service]);
    } else {
      setSelectedServices(
        selectedServices.filter(item => item.Item !== service.Item)
      );
    }
    service.checked = !service.checked;
  }

  function uncheckAll() {
    setSelectedServices([]);
    for (let i = 0; i < pricingSheets.length; i++) {
      const subsections = pricingSheets[i].subsections;
      for (let j = 0; j < subsections.length; j++) {
        const servicesForSubsection = subsections[j].services;
        for (let k = 0; k < servicesForSubsection.length; k++) {
          var service = servicesForSubsection[k];
          service.checked = false;
        }
      }
    }
  }

  const customTheme = createTheme({
    palette: {
      primary: {
        light: "#ffc400",
        main: "#ffab00",
        dark: "#778899",
        contrastText: "#fff"
      },
      secondary: {
        light: "#f0e6e6",
        main: "#c93434",
        dark: "#3c3c3c",
        contrastText: "#000"
      },
      mode: 'dark'
    }
  });

  return (
    <ThemeProvider theme={customTheme}>
      <div className="App">
        <header className="App-header">
          <img src={logo} height="50" width="50" />
          <div className="Header-text">
            Old School Boosting
          </div>
        </header>
        <div className="body">
          <div className="Posting-Space">
            <div className="UncheckAll" onClick={uncheckAll}>
              { selectedServices.length > 0
                            ? <div>[Uncheck all]</div>
                            : null
                          }
            </div>
            <ServiceSections className="ServiceDrawer" servicesData={pricingSheets} updateParent={checkboxUpdated} />
          </div>
          <div className="Posting-Space">
            <PostingSpace selectedServices={selectedServices} />
          </div>
        </div>
      </div>
    </ThemeProvider>
  );
}



export default App;
