import TextField from '@mui/material/TextField';
import TextareaCopyButton from '../text-area-copy-button/text-area-copy-button.js';
import Box from '@mui/material/Box';
import { useState, useEffect } from "react";

import styles from './posting-space.module.css';

export default function PostingSpace({ selectedServices }) {
  const [postingInfo, setPostingInfo] = useState({
    ticketNumber: '',
    questions: [],
    additionalInfo: '',
    posts: [],
    dibsPosts: [],
  });

  useEffect(() => {
    formatPostsForSelectedServices();
  }, [selectedServices, postingInfo.ticketNumber, postingInfo.additionalInfo]);

  function formatPostsForSelectedServices () {
    const questions = formatQuestionBlocksForSelectedServices();
    const posts = formatNormalPostsForSelectedServices();
    const dibsPosts = formatDibsPostsForSelectedServices();

    setPostingInfo({
      ticketNumber: postingInfo.ticketNumber,
      questions: questions,
      additionalInfo: postingInfo.additionalInfo,
      posts: posts,
      dibsPosts: dibsPosts,
    });
  }

  function formatQuestionBlocksForSelectedServices () {
    var questions = [];
    for (let i = 0; i < selectedServices.length; i++) {
      const selectedService = selectedServices[i];
      if (selectedService.questionsBlock) {
        questions.push({ 'text': selectedService.questionsBlock });
      }
    }

    return questions;
  }

  function formatNormalPostsForSelectedServices () {
    var posts = [];
    for (let i = 0; i < selectedServices.length; i++) {
      // If list contains object where key === selectedServices[i].Roles, add new line to that post
      // Else, create a new post where key is the role and value is the first line + the second line
      const selectedService = selectedServices[i];
      var index = posts.findIndex(p => p.role === selectedService.Roles)
      const quoting = selectedService.DisplayPrice === 'Quote';
      if (index === -1) {
        var newPostText = `<@&${selectedService.Roles}> #ticket-${postingInfo.ticketNumber} / ${postingInfo.ticketNumber}\n${selectedService.Item}`;
        if (!selectedService.quoting) {
          newPostText = newPostText + ` - ${selectedService.DisplayPrice}`;
        }
        const newPostObject = {
          'role': selectedService.Roles,
          'dibs': selectedService.dibs,
          'text': newPostText,
          'quoting': quoting,
        }
        posts.push(newPostObject);
      } else {
        var existingPost = posts[index]
        newPostText = `${existingPost.text}\n${selectedService.Item}`;
        if (!selectedService.quoting) {
          newPostText = newPostText + ` - ${selectedService.DisplayPrice}`;
        }
        const updatedPost = {
          'role': selectedService.Roles,
          'dibs': selectedService.dibs,
          'text': newPostText,
          'quoting': quoting,
        }
        posts[index] = updatedPost;
      }
    }

    // Add additional info for each post
    for (let i = 0; i < posts.length; i++) {
      if (postingInfo.additionalInfo) {
        posts[i].text = `${posts[i].text}\n${postingInfo.additionalInfo}`;
      }
      if (posts[i].quoting) {
        posts[i].text = `${posts[i].text}\n**Quote - ** #quoting `;
      }
    }

    return posts;
  }

  function formatDibsPostsForSelectedServices () {
      var dibsPosts = [];
      for (let i = 0; i < selectedServices.length; i++) {
        // If list contains object where key === selectedServices[i].Roles, add new line to that post
        // Else, create a new post where key is the role and value is the first line + the second line
        const selectedService = selectedServices[i];
        if (selectedService.dibs !== undefined) {
          var index = dibsPosts.findIndex(p => p.dibs === selectedService.dibs)
          const quoting = selectedService.DisplayPrice === 'Quote';
          if (index === -1) {
            let dibsPings = ``;
            const splitDibs = selectedService.dibs.split(', ');
            for (let j = 0; j < splitDibs.length; j++) {
              dibsPings = dibsPings + `@${splitDibs[j]} `;
            }
            var newPostText = `${dibsPings}#ticket-${postingInfo.ticketNumber} / ${postingInfo.ticketNumber}\n${selectedService.Item}`;
            if (!selectedService.quoting) {
              newPostText = newPostText + ` - ${selectedService.DisplayPrice}`;
            }
            const newPostObject = {
              'role': selectedService.Roles,
              'dibs': selectedService.dibs,
              'text': newPostText,
              'quoting': quoting,
            }
            dibsPosts.push(newPostObject);
          } else {
            var existingPost = dibsPosts[index]
            newPostText = `${existingPost.text}\n${selectedService.Item}`;
            if (!selectedService.quoting) {
              newPostText = newPostText + ` - ${selectedService.DisplayPrice}`;
            }
            const updatedPost = {
              'role': selectedService.Roles,
              'dibs': selectedService.dibs,
              'text': newPostText,
              'quoting': quoting,
            }
            dibsPosts[index] = updatedPost;
          }
        }
      }

      // Add additional info for each dibs post
      for (let i = 0; i < dibsPosts.length; i++) {
        if (postingInfo.additionalInfo) {
          dibsPosts[i].text = `${dibsPosts[i].text}\n${postingInfo.additionalInfo}`;
        }
      }

      return dibsPosts;
    }

  function handleTicketNumberChange (event) {
    setPostingInfo({
      ticketNumber: event.target.value,
      questions: postingInfo.questions,
      additionalInfo: postingInfo.additionalInfo,
      posts: postingInfo.posts,
      dibsPosts: postingInfo.dibsPosts,
    });
  }

  function handleAdditionalInfoChange (event) {
    setPostingInfo({
      ticketNumber: postingInfo.ticketNumber,
      questions: postingInfo.questions,
      additionalInfo: event.target.value,
      posts: postingInfo.posts,
      dibsPosts: postingInfo.dibsPosts,
    });
  }

  const questionPosts = postingInfo.questions.map(question =>
    <TextareaCopyButton
      editable={true}
      value={question.text}
    />
  );

  const dibsPostings = postingInfo.dibsPosts.map(dibsPost =>
    <TextareaCopyButton
      editable={false}
      value={dibsPost.text}
    />
  );

  const postings = postingInfo.posts.map(post =>
    <TextareaCopyButton
      editable={false}
      value={post.text}
    />
  );

  return (
    <Box
      component="form"
      sx={{
        '& .MuiTextField-root': { m: 1, width: '35rem' },
      }}
      noValidate
      autoComplete="off"
    >
      <div>
        <TextField
          multiline
          required
          label="Ticket Number"
          id="outlined-required"
          value={postingInfo.ticketNumber}
          onChange={(event) =>
            handleTicketNumberChange(event)
          }
        />
      </div>
      <TextField
        multiline
        label="Additional Information"
        id="outlined-required"
        value={postingInfo.additionalInfo}
        onChange={(event) =>
          handleAdditionalInfoChange(event)
        }
        minRows={3}
      />
      { questionPosts.length > 0
        ? <div className={styles.questionBlock}>
            <div className={styles.postingLabels}>Questions for customer</div>
            <div className={styles.questions}>
              {questionPosts}
            </div>
          </div>
        : null
      }
      { dibsPostings.length > 0
        ? <div className={styles.postingBlock}>
            <div className={styles.postingLabels}>Staff Dibs</div>
            <div className={styles.postings}>
              {dibsPostings}
            </div>
          </div>
        : null
      }
      { postings.length > 0
        ? <div className={styles.postingBlock}>
            <div className={styles.postingLabels}>Posts</div>
            <div className={styles.postings}>
              {postings}
            </div>
          </div>
        : null
      }
    </Box>
  );
}