import axios from "axios";
import * as xlsx from "xlsx/xlsx";
import questionsForService from "./questions-for-service.json";

export const fetchPricingData = async () => {
  const pricingSheets = await axios({
    url: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vSg9Ruap-_Lym2iAzhVNXpNXgZwUwZaM5-n0Wxa6Y19jRpL5dzF7i_wuxLD3MPSdzLkdsQiprcDQoei/pub?output=xlsx',
    method: 'GET',
    responseType: 'blob',
  })
    .then(async function (response) {
      // handle success
      var workbook = xlsx.read(await response.data.arrayBuffer(), { type: "array" });

      var pricingSheets = [];

      // Skip the first sheet since that just contains the message IDs for the pricing post
      for (let i = 1; i < workbook.SheetNames.length; i++) {
        const workbookSheet = workbook.Sheets[workbook.SheetNames[i]];
        const jsonSheetServices = xlsx.utils.sheet_to_json(workbookSheet)
        const jsonSheet = {
          title: workbook.SheetNames[i],
          services: jsonSheetServices
        }
        pricingSheets.push(jsonSheet)
      }

      return pricingSheets
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
    .finally(function () {
      // always executed
    });
    const massagedData = MassageData(pricingSheets);
    return massagedData;
};

function MassageData(pricingSheets) {
  var removedNotes = RemoveNotes(pricingSheets);
  var splitSections = SplitSectionsByHeader(removedNotes);
  var sectionsWithFormattedPricing = FormatPrice(splitSections);

  return sectionsWithFormattedPricing
}

function FormatPrice(sections) {
  for (let i = 0; i < sections.length; i++) {
    const subsections = sections[i].subsections;
    for (let j = 0; j < subsections.length; j++) {
      const servicesForSubsection = subsections[j].services;
      for (let k = 0; k < servicesForSubsection.length; k++) {
        var service = servicesForSubsection[k];
        // This definitely shouldn't go here, but I'm doing it anyway.
        // I don't want to go three levels deep to map a new property to service objects later.
        service.checked = false;

        // Also not what this method was for, but same thing above.
        // I want to rename the combat achievement bundles so they can be differentiated for dibs.
        if (service.Item === "Minimal participation" && service.Heading === "ToB Entry Mode CA Bundle - <:ironman:1123131721836138516> Ironman Friendly") {
          service.Item = "ToB minimal participation bundle";
        } else if (service.Item === "Participation" && service.Heading === "ToB Entry Mode CA Bundle - <:ironman:1123131721836138516> Ironman Friendly") {
          service.Item = "ToB participation bundle";
        } else if (service.Item === "Full leech" && service.Heading === "Regular and CM CoX CA Bundle - <:ironman:1123131721836138516> Ironman Friendly") {
          service.Item = "CoX full leech bundle";
        } else if (service.Item === "Participation" && service.Heading === "Regular and CM CoX CA Bundle - <:ironman:1123131721836138516> Ironman Friendly") {
          service.Item = "CoX participation bundle";
        }

        // The actual thing this method was for.
        if  (service.Price === 'Quote') {
          service.DisplayPrice = 'Quote';
        } else {
          service.DisplayPrice = service.Price.toString() + 'm';
          if (service.Notes) {
            service.DisplayPrice = service.DisplayPrice + ' (' + service.Notes + ')';
          }
        }
        sections[i].subsections[j].services[k] = service;
      }
    }
  }
  return sections;
}

function SplitSectionsByHeader(sections) {
  var splitSections = [];

  // Nex, Nightmare, Miscellaneous, Barbarian Assault, and Pet boosting are all single sections
  // This makes the page long when split, so I'd like to hard code the section title to "Other"
  var otherSubsection = [];

  for (let i = 0; i < sections.length; i++) {

    const groupedSection = Object.groupBy(sections[i].services, ({ Heading }) => Heading);
    // Sections that have groupmates (i.e. toa has multiple, will be built into a subsection and added here
    var subsections = [];
    for (const [key, value] of Object.entries(groupedSection)) {
      const keyTrimmed = key.split(' - ')[0]; // Remove <1234589071> ironman friendly
      var keyToUse;
      const detailHeaderNotNeeded = ['Miscellaneous', 'Barbarian Assault', 'Pet Boosting'];
      const generalHeaderNeeded = ['Nex', 'Nightmare'];
      if (detailHeaderNotNeeded.includes(keyTrimmed)) {
        keyToUse = sections[i].title;
        const generalSubsection = {
          title: keyToUse,
          services: value,
        }
        otherSubsection.push(generalSubsection);
      } else if (generalHeaderNeeded.includes(sections[i].title)) {
        keyToUse = sections[i].title;
        const generalSubsection = {
          title: keyToUse,
          services: value,
        }
        otherSubsection.push(generalSubsection);
      } else {
        // These would be the services that each have their own subsection (i.e. all toa in one subsection)
        keyToUse = keyTrimmed;
        const subsection = {
          title: keyToUse,
          services: value,
        }
        subsections.push(subsection);
      }
    }
    if (subsections.length > 0) {
      const splitSubsections = {
        title: sections[i].title,
        subsections: subsections,
      }
      splitSections.push(splitSubsections)
    }
  }
  const otherSubsections = {
    title: 'Other',
    subsections: otherSubsection,
  }
  splitSections.push(otherSubsections)
  return splitSections;
}

function RemoveNotes(pricingSheets) {
  for (let i = 0; i < pricingSheets.length; i++) {
    const filteredServices = pricingSheets[i].services.filter(item => item.Heading !== 'Notes');
    pricingSheets[i].services = filteredServices
  }
  return pricingSheets;
}