import axios from "axios";
import * as xlsx from "xlsx/xlsx";

export const fetchAdditionalData = async () => {
  const myAdditions = await axios({
    url: 'https://docs.google.com/spreadsheets/d/e/2PACX-1vSCo3rmLexEbqQTvI96LXrbyXM80SMeZDQcw9WuduHgotrmZHSwfSQydJa4lKqz5JiDczKD93sqtLs_/pub?output=xlsx',
    method: 'GET',
    responseType: 'blob',
  })
    .then(async function (response) {
      // handle success
      var workbook = xlsx.read(await response.data.arrayBuffer(), { type: "array" });

      var additionsSheets = [];

      // The first sheet is Dibs and Questions by Item
      const firstSheet = workbook.Sheets[workbook.SheetNames[0]];
      const jsonDibsAndQuestions = xlsx.utils.sheet_to_json(firstSheet);

      // The second sheet is Availability by Discord ID

      const myAdditions = {
        dibsAndQuestions: jsonDibsAndQuestions
      }

      return myAdditions;
    })
    .catch(function (error) {
      // handle error
      console.log(error);
    })
    .finally(function () {
      // always executed
    });
    return myAdditions;
};